<template>
  <div class="videoCard">
    <h3
      class="recomTitle"
      :style="{'display': content[0].prop.title=='' ? 'none' : 'block'}"
      style="text-align:center;"
      v-html="content[0].prop.title"
    ></h3>
    <van-swipe
      :autoplay="swipeTime"
      indicator-color="white"
      @change="onChange"
      :show-indicators="false"
    >
      <van-swipe-item v-for="(item,index) in content[0].ads" :key="index">
        <video-player
          class="video-player vjs-custom-skin"
          ref="videoPlayer"
          :playsinline="true"
          :webkit-playsinline="true"
          :options="playerOptions[index]"
          @play="onPlayerPlay($event)"
          @pause="onPlayerPause($event)"
          @ended="onPlayerEnded($event)"
        ></video-player>
        <router-link
          :to="{ path: '/appDetails', query: { contentid:content[0].items[index].contentid}}"
        >
          <div class="appClass">
            <div class="appC">
              <div class="iconImage">
                <img :src="content[0].items[index].iconurl" alt />
              </div>
              <div class="appIntroduced">
                <span class="appName" v-text="content[0].items[index].name">王都荣耀</span>
                <br />
                <span
                  class="appFlow"
                  v-text="(content[0].items[index].appsize/1024).toFixed(2)+'M'"
                ></span>
              </div>
              <div class="buttonClass">
                <van-button
                  :url="content[0].items[index].orderurl"
                  class="downButton"
                  round
                  plain
                  type="info"
                  size="small"
                >下载</van-button>
              </div>
            </div>
          </div>
        </router-link>
      </van-swipe-item>
    </van-swipe>
    <div v-if="noblockline==true" class="separate"></div>
  </div>
</template>

<script>
import {
  Swipe,
  SwipeItem,
  Button
} from "vant";
export default {
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Button.name]: Button
  },
  computed: {
    player() {
      return this.$refs.videoPlayer.player;
    }
  },
  data() {
    return {
      active: 0,
      swipeTime: 5000,
      playerOptions: []
    };
  },
  props: ["content", "noblockline"],
  created() {
    this.playerOptionsMethod();
  },
  mounted() {},
  methods: {
    //开始时
    onPlayerPlay($event) {
      console.log($event);
      this.swipeTime = null;
      $event.play();
    },
    //暂停时
    onPlayerPause($event) {
      console.log($event);
    },
    //结束时
    onPlayerEnded($event) {
      this.swipeTime = 5000;
    },
    onChange(index) {
      var idx = index - 1 < 0 ? this.playerOptions.length - 1 : index - 1;
      // console.log("当前 Swipe 索引：" + index, idx);
      // this.swipeTime = 5000;
      var a = this.playerOptions[idx].sources[0].src;
      this.playerOptions[idx].sources[0].src = "";
      this.playerOptions[idx].sources[0].src = a;
      this.swipeTime = 5000;
    },
    playerOptionsMethod() {
      console.log("content", this.content[0].ads);
      for (var i = 0; i <= this.content[0].ads.length - 1; i++) {
        this.playerOptions.push({
          playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
          autoplay: false, //如果true,浏览器准备好时开始回放。
          muted: false, // 默认情况下将会消除任何音频。
          loop: false, // 导致视频一结束就重新开始。
          preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
          language: "zh-CN",
          aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
          fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
          sources: [
            {
              type: "video/mp4",
              src: this.content[0].ads[i].playUrl //url地址
              // src:
              //   "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm"
            }
          ],
          poster: this.content[0].ads[i].picUrl, //你的封面地址
          // poster: "https://img.yzcdn.cn/vant/apple-4.jpg",
          // width: document.documentElement.clientWidth,
          notSupportedMessage: "此视频暂无法播放，请稍后再试", //允许覆盖Video.js无法播放媒体源时显示的默认信息。
          controlBar: {
            timeDivider: true,
            durationDisplay: true,
            remainingTimeDisplay: false,
            fullscreenToggle: true //全屏按钮
          }
        });
      }
    }
  }
};
</script>

<style lang="less">
.videoCard {
  .van-swipe-item {
    text-align: center;
    img {
      width: 333.33px;
      height: 152.7px;
    }
    .iconImage {
      float: left;
      width: 45px;
      height: 45px;
      margin: 0 10px;
      overflow: hidden;
      border-radius: 9.72px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .appClass {
      margin-top: 13.88px;
      width: 333.33px;
      display: inline-block;
      padding-bottom: 13.88px;
      .appC {
        text-align: left;
        .appIntroduced {
          display: inline-block;
          .appName {
            line-height: 25px;
            font-size: 16.6px;
            font-weight: bold;
            color: #212121;
            width: 200px;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
          }
          .appFlow {
            line-height: 24px;
            color: #b8b8b8;
            font-size: 12px;
          }
        }
        .buttonClass {
          float: right;
          padding-top: 8px;
          box-sizing: border-box;
          .downButton {
            float: none;
            overflow: hidden;
            margin-bottom: 3px;
          }
        }
      }
    }
  }
  .van-tabbar--fixed {
    position: relative;
  }
}
.vjs-custom-skin > .video-js .vjs-big-play-button {
  /*
  播放按钮换成圆形
  */
  height: 2em;
  width: 2em;
  line-height: 2em;
  border-radius: 1em;
  border: none;
  left: 55%;
}
.video-player {
  width: 333.33px;
  display: inline-block;
}
.recomTitle {
  font-size: 16.6px;
  margin: 14px 0;
}
.separate {
  height: 6px;
  background: rgba(242, 242, 242, 1);
}
</style>
