<template>
  <!-- <van-tabs v-model="active" swipeable> -->
  <div style="margin-top:10px" class="horizon">
    <van-cell
      :style="{'display': propTitle ? 'none' : '','font-size': '16.66px','font-weight':'bold'}"
      :title="contitle"
      :border="false"
      title-style="text-align:center"
    />
    <div class="slide-box">
      <div v-for="(item,index) in content[idx].items" class="slide-item" :key="index">
        <router-link
          :to="{ path: '/appDetails', query: { contentid: item.contentid||item.orderUrl.split('cid=')[1].split('&gid')[0]}}"
        >
          <div class="appIcon">
            <img :src="item.iconurl||item.iconUrl" alt />
          </div>
          <span class="appName" v-html="item.name"></span>
          <span
            class="flow"
            v-html="(item.appsize?item.appsize/1024:item.appSize/1024).toFixed(2)+'M'"
          ></span>
        </router-link>
        <van-button
          class="downButton"
          :url="item.orderurl||item.orderUrl"
          round
          plain
          type="info"
          size="small"
        >下载</van-button>
      </div>
    </div>
    <div v-if="noblockline==true" class="separate"></div>
  </div>
</template>

<script>
import { Button,Cell } from "vant";
import axios from "axios";
export default {
  components: {
    [Button.name]: Button,
    [Cell.name]: Cell,
  },
  computed: {
    contitle: {
      get: function() {
        if (this.content.length >= 2) {
          this.idx = 1;
          this.propTitle = false;
          this.detailurl = this.content[0].items[0].detailurl;
          console.log("idx", this.idx);
          return this.content[0].items[0].name;
        } else {
          this.idx = 0;
          this.propTitle = true;
          this.detailurl = "";
        }
      }
    }
  },
  data() {
    return {
      active: 0,
      idx: 0,
      propTitle: true,
      detailurl: ""
    };
  },
  props: ["content", "noblockline"],
  //主要内容，标题，是否有分隔线
  mounted() {},
  methods: {}
};
</script>

<style lang="less">
.slide-box {
  display: -webkit-box;
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;
}
.slide-item {
  width: 100px;
  padding-bottom: 10.88px;
  // margin-left: 13.88px;
  text-align: center;
  .appIcon {
    width: 59px;
    height: 59px;
    display: inline-block;
    margin-bottom: 9px;
    border-radius: 9.72px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .flow {
    display: inline-block;
    color: #9a9a9a;
    margin-bottom: 5px;
    font-size: 12px;
    min-width: 59px;
  }
  .appName {
    display: block;
    color: #212121;
    margin-bottom: 5px;
    font-size: 16.6px;
    font-weight: bold;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
  .downButton {
    float: none;
    overflow: hidden;
    margin-bottom: 3px;
    &::before{
      display: none;
    }
  }
}
.slide-box::-webkit-scrollbar {
  display: none; /*不展示横向滚动条*/
}
.separate {
  height: 6px;
  background: rgba(242, 242, 242, 1);
}
.van-cell--large .van-cell__title {
  font-size: 16px;
  font-weight: bold;
}
.horizon {
  .van-cell--borderless {
    border-bottom: none;
  }
  .slide-box {
    border-top: none;
  }
}
</style>

